/* Parallax Background */
body{
    margin: 0;
    overflow: hidden;
    overflow-y: hidden;
}

#parallax {
  position: relative;
  width: 100%;
  height: 100%;
  /* background-image: url(./images/alex-in-den-haag.jpg); */
  background-image: url(/Professional-Portfolio-React/static/media/alex-in-den-haag-1,5.60145aeb.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

#intro {
    position: relative;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.gibson-placard {
    margin-top: 150px;
    color: var(--bauhausWhite);
    opacity: .85;
    font-size: clamp(24px, 46px, 70px);
    text-shadow: 6px 0px 0px var(--bauhausBlack), -6px 0px 0px var(--bauhausRed), 0px 6px 0px var(--rotterdamPlum), 0px -6px 0px var(--rotterdamClementine);
}


.subline {
    font-size: 2.2rem;
    line-height: 1.6em;
    vertical-align: middle;
    color: var(--bauhausWhite);
    opacity: .85;
    font-size: clamp(18px, 22px, 28px);
    text-shadow: 0px 3px 0px var(--rotterdamRot), 0px 6px 0px var(--rotterdamClementine);
}

@media screen and (max-width: 768px) {
    body {
    overflow: inherit;
    }
}

@media screen and (min-width: 768px) {
    .gibson-placard {
        font-size: 70px;
        }

    .subline {
        font-size: 28px;
    }
    
}
