.profile-pic {
    max-width: 350px;
    margin: 2rem 3rem 2rem 1rem;
    border-radius: 5%;
    flex: 1 1 300px;
    box-shadow: 7px -7px 1px var(--bauhausYellowBlur), -7px 10px 1px var(--bauhausRedBlur), 12px 4px 1px var(--bauhausBlueBlur);
    transition: 300ms ease-out;
}


/* hover over shadows bisection-1*/
.bisection-1:hover > .profile-pic {
    box-shadow: 15px -15px 2px var(--bauhausYellowBlur), -15px 20px 2px var(--bauhausRedBlur), 25px 8px 2px var(--bauhausBlueBlur);
}

.bisection-1:hover > .bisection-1-h1 {
    text-shadow: -3px 3px 0px var(--rotterdamRot), -6px 6px 0px var(--rotterdamPink);
}

.bisection-1:hover > p {
    background-color: rgb(244, 244, 244, .8);
}


/* hover over shadows bisection-2*/
.bisection-2:hover > .bisection-2-h1 {
    text-shadow: 3px 3px 0px var(--rotterdamBlue), 6px 6px 0px var(--rotterdamTeal);
}

.bisection-2:hover > .skill-box > .skills-ul > .skills-li {
    text-shadow: 0px 3px 0px var(--bauhausYellowBlur), 3px 4px 0px var(--rotterdamLavender);
    transform: 1.05;
}


